$yellow: #f4d35e;
$black: #000000;

.no-full-box {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.full-box {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.bg-yellow {
    background-color: $yellow;
}

.bg-black {
    background-color: $black;
}

.section-title {
    font-family: 'Russo One', sans-serif;
    font-size: 2em;
    margin-bottom: 0.3em;
}